nav.custom-navbar-menu {
    margin: 20px auto;
    // background: #f2f2f2;
    border-radius: 2px; 
    ul{
        list-style: none;
        padding: 0;
        background: #f6f6fa;
        li{            
            display: inline-block;
            padding: 15px 10px;
            a{
                text-transform: uppercase;
                padding: 10px 10px;
            }
            a.active{
                color: #FF5722;
                border-bottom: 2px solid #FF5722;
                padding: 15px 10px;
                transition: all 0.2s ease-in;
                background: #ff57220a;
            }
            a:hover,a:active,a:focus{
                text-decoration: none;
            }
        }
    }
}
.card-box{
    background: #f2f2f2;
    padding: 20px;
    border-radius: 2px;
}
.mh300{
    min-height: 300px;
}